.product {
	&__img {
		img {
			width: 70px;
		}
	}
	&__title {
		font-size: 18px;
		font-weight: bold;
		@media screen and (max-width: 1024px) {
			font-size: 14px;
		}
		@media screen and (max-width: 767px) {
			font-weight: normal;
		}
	}
	del {
		color: #f00;
	}
	.price {
		color: #0f0;
	}
}
